body {
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  margin: 0;
  color: #fff;
}

* {
  scroll-behavior: smooth;
}

::-webkit-scrollbar-track {
  background: #2e1854;
}

::-webkit-scrollbar-thumb {
  background: #b01ba5;
}

::-webkit-scrollbar {
  width: 10px;
}
